import React from 'react'
import {AnswerButton} from '../components/AnswerButton'
import {AnswerValue} from '../components/AnswerValue'
import { useHistory } from 'react-router-dom'
import '../assets/style/questionsPage.css'

export const QuestionsPage:React.FC = () => {
  const todo = [
    {},
    {},
    {},
    {},
  ]

  
  
  return (
    <div className="container-fluid gray">
      <div className="questions-page">
        <div className="questions-page__body">
          <h2>How old your elder brother was 10 years before you was born, mate?</h2>
          <div className="answer-option">
            {todo.map( param => {
              return (
                <AnswerButton />
              )
            })}
          </div>
        </div>
        <div className="questions-page__score">
          {todo.map( param => {
              return (
                <AnswerValue />
              )
            })}
          

        </div>
      </div>
    </div>
  )
} 